import React from 'react';

import AnimalList from './AnimalList';

import '../styles/_CatchedList.scss';

const CatchedList = (props) => {
    const done = props.tasks.filter(task => !task.active);
    const doneTasks = done.map(task => <AnimalList key={task.id} task={task} delete={props.delete} change={props.change} />)

    return (
        <div className="list-wrapper">
            <h6 className="title"><strong>LISTA DODANYCH ZWIERZƒÑT</strong></h6>
            <div className="list">
                {doneTasks.length === 0 && < p className='default'>Tu pojawiƒÖ siƒô Twoje ostatnio z≈Çapane zwierzƒôta</p>}
                {doneTasks.length > 4 && <span className="info">(Wy≈õwietlane sƒÖ jedynie 4 ostatnio dodane zwierzaki)</span>}
                {doneTasks.slice(0, 4)}
            </div>
        </div >
    );

}

export default CatchedList;